



import request from '@/utils/request.js'

module.exports = {
	pay (options) {
		console.log(options)
		// 支付方式
		let payType = options.pay_type || 'wechat'
		
		// 支付方式待选择
		if (payType === 'select_pending') {
			this.selectPayMethod(options)
			return false
		}
		

		// 微信系支付
		uni.requestPayment({
			...options.pay_config,
			success: () => {
				options.success && options.success()
			},
			fail: () => {
				options.fail && options.fail()
			}
		})

		



















		









































		





















		
















		

















	},
	selectPayMethod(options) {
		
		uni.showActionSheet({
			itemList: ['支付宝支付', '微信支付'],
			success: (res) => {
				let payTypeList = ['alipay', 'wechat']
				let payType = payTypeList[res.tapIndex]
				// console.log(`/orders/${options.order.uuid}/payment-config`)
				
				uni.showLoading({
					title: '加载中~'
				})
				
				request(`/orders/${options.order.uuid}/payment-config`, 'POST', {
					pay_type: payType
				}).then(r => {
					uni.hideLoading()
					// console.log('rrrrr ==>', r)
					options = {
						...options,
						...r.data
					}
					this.pay(options)
				})
			},
			fail: (res) => {
				console.log(res.errMsg);
			}
		})
		
	}
}
